.slick-arrow{height:100%;width:32px;border:1px solid #b4bdc3;border-radius:48px}@media(max-width: 640px){.slick-arrow{display:none !important}}.slick-prev{left:0;z-index:999}.slick-prev::before{color:#0f0f11}.slick-next{right:0}.slick-next::before{color:#0f0f11}.slick-dots li button:before{content:"▄▄▄"}